
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.todo-wrapper{
 margin: 20px auto 20px auto;
 width: 450px;
 min-height: 600px;
 border: 0.5rem dotted grey;
 padding: 20px;
 text-align: center;
 font-family: 'Cabin', sans-serif;
}

.btn {
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  border-width: 2px;
  border-radius: 3px;
  border-style: solid;
  padding: 8px;
}
.btn-primary {
  color: mediumvioletred;
  border-color: lightgray;
  background-color: transparent;
}
input {
  width: 80%;
  border: none;
  border-radius: 5px;
  border: 2px solid lightgray;

  margin-right: 10px;
  padding: 5px;
  line-height: 17px;
}


.todoWrapper {
  border-bottom: 1px solid #efefef;
  text-align: left;
  margin-bottom: 10px;
  margin-top: 20px;
}
.removeTodo {
  margin: 20px;
  margin-bottom: 10px;
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  border-width: 2px;
  border-radius: 3px;
  border-style: solid;
  padding: 8px;
  color: rgba(203, 20, 32, 0.4);
  border-color: rgba(203, 20, 32, 0.4);
  background-color: transparent;
}
.done {
  margin: 0px;
  width: 2rem;
}
.workDone {
  text-decoration: line-through;
}
